
<template>
  	<div class="app-calendar border reservation">

		
		<!-- ADD EVENT -->
		<vs-prompt
			class="calendar-event-dialog position-relative"
			title="새로운 예약"
			accept-text= "예약 추가"
			cancel-text= "취소"
			button-cancel = "bg_pale"
			button-accept = "border"
			@accept="addEvent"
			:active.sync="activePromptAddEvent"
			>

			<header class="vs-dialog-header custom_header">
				<div class="con-title-after">
					<span class="after"></span>
					<h3 class="dialog-title">새로운 예약</h3>
				</div>
			</header>

			<div class="calendar__label-container flex">
				<div class="vs-con-dropdown parent-dropdown ml-auto my-2 cursor-pointer">
					<div class="con-vs-chip badge vs-chip-null badge" :style="`background-color: ${labelLocal.color}`" @click="colorLabelPp = !colorLabelPp;"><span class="text-chip vs-chip--text">색상</span></div>

					<div class="con-vs-dropdown--menu vs-dropdown-menu" v-show="colorLabelPp">
						<div class="vs-dropdown--custom vs-dropdown--menu" style="z-index: 200001">
							<div class="vs-component-link-item">
								<div v-for="(label, index) in calendarLabels" :key="index" @click="clickLabel(label)" class="vs-component vs-dropdown--item cursor-pointer">
									<div class="vs-dropdown--item-link">
										<div class="h-3 w-3 inline-block rounded-full"  :style="`background-color: ${label.color}`"></div>
									</div>
								</div>
							</div>
							<div class="vs-dropdown--menu--after" style="top: 10px;"></div>
						</div>
					</div>
				</div>
			</div>
		
			<div class="flex align-center">
				<div class="w-2/4 mr-1" >
					<vs-input icon="search" placeholder="고객명" v-model="name"/>
				</div>
				<div class="w-1/4 mr-1" >
					<vs-button color="primary" type="border" class="btn small">신규 고객</vs-button>
				</div>
				<div class="w-1/4">
					<vs-button color="primary" type="border" class="btn small">정보 확인</vs-button>
				</div>
			</div>

			
			<div class="flex align-center mt-4 time_box">
				<div class="w-1/3 mr-1">
					<small class="date-label">날짜</small>
					<datepicker :language="langKo" name="start-date" v-model="startDate" format="yyyy-MM-dd"></datepicker>
				</div>
				<div class="w-1/3 mr-1">
					<small class="date-label">시작 시간</small>
					<!-- pc input -->
					<!-- <vs-input class="w-full hidden sm:hidden md:block"  v-model="time" type="time"></vs-input> -->
					<vs-input class="w-full"  v-model="time" type="time"></vs-input>

					<!-- 모바일 input -->
					<!-- <vs-input icon-after="true" icon="alarm" placeholder="-- : -- : --" v-model="time" class="w-full block sm:block md:hidden border-left-none" @click="timePickerPp = true" readonly/> -->

				</div>
				<div class="w-1/3 mr-1">
					<small class="date-label">진행 시간(분)</small>
					<vs-input class="w-full" type="number" min="1" value="50"></vs-input>
				</div>
			</div>

			<div class="mt-4">
				<ul class="flex">
					 <li class="mr-4">
						<vs-radio v-model="radios1" vs-value="video">화상상담</vs-radio>
					</li>
					<li class="mr-4">
						<vs-radio v-model="radios1" vs-value="call">전화상담</vs-radio>
					</li>
					<li class="mr-4">
						<vs-radio v-model="radios1" vs-value="meet">대면상담</vs-radio>
					</li>
				</ul>
			</div>

			<div>
				<vs-checkbox v-model="checkBox1" class="ml-0 mt-5 mb-2">중복</vs-checkbox>
				<div v-if="checkBox1" class="flex align-items-center">
					<span>중복</span>
					<v-select :searchable="false" :taggable="true" readonly class="small_select mx-2" v-model="select1" :options="options1" :clearable="false"></v-select>
					<span>번</span>
				</div>
			</div>
			
			<div class="flex mt-5">
				<div class="w-2/3 mr-1">
					<small class="date-label">종류</small>
					<v-select :searchable="false" :taggable="true" inputmode="none" class="w-full" v-model="select2" :options="options2" :clearable="false"></v-select>
				</div>
				<div class="w-1/3" >
					<small class="date-label">비용(원)</small>
					<vs-input type="number" class="w-full" v-model="price"></vs-input>
				</div>
			</div>

			<vs-divider position="left" class="my-6"></vs-divider>

			<vs-input class="w-full" placeholder="메모" v-model="memo"></vs-input>

			<div class="flex mt-5 align-items-center">
				<div class="w-1/3">
					결제방법
				</div>
				<div class="w-2/3 mr-1">
					<v-select :searchable="false" :taggable="true" readonly class="w-full" v-model="select3" :options="options3" :clearable="false"></v-select>
				</div>
			</div>

		</vs-prompt>

		<!-- 모바일 time picker -->
		<!-- <div class="mb_time_picker_pp xs:block sm:block md:hidden" v-if="timePickerPp">
			<div class="dimm" @click="timePickerPp = false"></div>
			<div class="inner">
				<smooth-picker ref="smoothPicker" :data="timeData"/>
				<vs-button @click="confirmTime" class="block w-5/6 mx-auto">확인</vs-button>
			</div>
		</div> -->

		<!-- EDIT EVENT -->
		<vs-prompt
			class="calendar-event-dialog edit position-relative"
			title="예약 정보"
			:active.sync="activePromptEditEvent">

			<header class="vs-dialog-header custom_header">
				<div class="con-title-after">
					<span class="after"></span>
					<h3 class="dialog-title">예약 정보 </h3>
				</div>
			</header>

			<div class="calendar__label-container flex">
				<div class="vs-con-dropdown parent-dropdown ml-auto my-2 cursor-pointer">
					<div class="con-vs-chip badge vs-chip-null badge" :style="`background-color: ${labelLocal.color}`" @click="colorLabelPp = !colorLabelPp;"><span class="text-chip vs-chip--text">색상</span></div>

					<div class="con-vs-dropdown--menu vs-dropdown-menu" v-show="colorLabelPp">
						<div class="vs-dropdown--custom vs-dropdown--menu" style="z-index: 200001">
							<div class="vs-component-link-item">
								<div v-for="(label, index) in calendarLabels" :key="index" @click="clickLabel(label)" class="vs-component vs-dropdown--item cursor-pointer">
									<div class="vs-dropdown--item-link">
										<div class="h-3 w-3 inline-block rounded-full"  :style="`background-color: ${label.color}`"></div>
									</div>
								</div>
							</div>
							<div class="vs-dropdown--menu--after" style="top: 10px;"></div>
						</div>
					</div>
				</div>

			</div>
		
			<div class="flex align-itmes-center">
				<div class="w-3/4 mr-1" >
					<vs-input icon="search" class="w-full" placeholder="고객명" v-model="name"/>
				</div>
				<div class="w-1/4">
					<vs-button color="primary" type="border" class="btn">정보 확인</vs-button>
				</div>
			</div>

			
			<div class="mt-5 align-items-center price_box">
				<ul>
					<li>
						<vs-radio v-model="radios2" vs-value="80000">80000 원</vs-radio>
					</li>
					<li>
						<vs-radio v-model="radios2" vs-value="other">다른 가격</vs-radio>
						<div v-if="radios2 == 'other'" class="mb-2">
							<vs-input type="number" class="w-full" v-model="price"></vs-input>
						</div>			
					</li>
				</ul>
				<vs-button color="primary" class="btn">결제 등록</vs-button>

				<div class="flex mt-2">
					<v-select :searchable="false" :taggable="true" readonly class="w-1/2 mr-1" v-model="select4" :options="options4" :clearable="false"></v-select>
					<v-select :searchable="false" :taggable="true" readonly class="w-1/2" v-model="select5" :options="options5" :clearable="false"></v-select>
				</div>
			</div>

			<vs-divider position="left"></vs-divider>
			
			<div class="flex align-center mt-4 time_box">
				<div class="w-1/3 mr-1">
					<small class="date-label">날짜</small>
					<datepicker :language="langKo" name="start-date" v-model="startDate" format="yyyy-MM-dd"></datepicker>
				</div>
				<div class="w-1/3 mr-1">
					<small class="date-label">시작 시간</small>
					<!-- pc input -->
					<!-- <vs-input class="w-full"  v-model="time" type="time"></vs-input> -->
					<vs-input class="w-full"  v-model="time" type="time"></vs-input>

					<!-- 모바일 input -->					
					<!-- <vs-input icon-after="true" icon="alarm" placeholder="-- : -- : --" v-model="time" class="w-full block sm:block md:hidden border-left-none" @click="timePickerPp = true"  readonly/> -->
				</div>
				<div class="w-1/3 mr-1">
					<small class="date-label">진행 시간(분)</small>
					<vs-input class="w-full" type="number" min="1" value="50"></vs-input>
				</div>
			</div>

			<div class="mt-4">
				<ul class="flex">
					 <li class="mr-4">
						<vs-radio v-model="radios1" vs-value="video">화상상담</vs-radio>
					</li>
					<li class="mr-4">
						<vs-radio v-model="radios1" vs-value="call">전화상담</vs-radio>
					</li>
					<li class="mr-4">
						<vs-radio v-model="radios1" vs-value="meet">대면상담</vs-radio>
					</li>
				</ul>
			</div>

			<div>
				<vs-checkbox v-model="checkBox1" class="ml-0 mt-5 mb-2">중복</vs-checkbox>
				<div v-if="checkBox1" class="flex align-items-center">
					<span>중복</span>
					<v-select :searchable="false" :taggable="true" readonly class="small_select mx-2" v-model="select1" :options="options1" :clearable="false"></v-select>
					<span>번</span>
				</div>
			</div>
			
			<div class="flex mt-5">
				<div class="w-2/3 mr-1">
					<small class="date-label">종류</small>
					<v-select :searchable="false" :taggable="true" readonly class="w-full" v-model="select2" :options="options2" :clearable="false"></v-select>
				</div>
				<div class="w-1/3" >
					<small class="date-label">비용(원)</small>
					<vs-input type="number" class="w-full" v-model="price"></vs-input>
				</div>
			</div>

			<vs-divider position="left" class="my-6"></vs-divider>

			<vs-input class="w-full" placeholder="메모" v-model="memo"></vs-input>

			<div class="flex mt-5 align-items-center">
				<div class="w-1/3">
					결제방법
				</div>
				<div class="w-2/3 mr-1">
					<v-select :searchable="false" :taggable="true" readonly class="w-full" v-model="select3" :options="options3" :clearable="false"></v-select>
				</div>
			</div>

			<footer class="custom_footer">
				<button type="button" name="button" class="vs-component vs-button vs-dialog-accept-button vs-button-primary vs-button-border" style="background: transparent;">
					<span class="vs-button-text vs-button--text" @click="editEvent">
						확인
					</span>
					<span class="vs-button-linex" style="top: auto; bottom: -2px; left: 50%; transform: translate(-50%);"></span>
				</button>
				<button type="button" name="button" class="vs-component vs-button vs-dialog-cancel-button vs-button-primary vs-button-bg_pale" @click="activePromptEditEvent = false">
					<span class="vs-button-text vs-button--text">
						취소
					</span>
					<span class="vs-button-linex" style="top: auto; bottom: -2px; left: 50%; transform: translate(-50%);"></span>
				</button>

				<div class="left_bottom_menu" >
					<vs-button color="danger" type="border" class="px-4" @click="removeEvent">
						<i class="feather icon-trash text-danger"></i>
					</vs-button>
				</div>
			</footer>

		</vs-prompt>



		<div class="row no-gutters">
			<!-- Calendar -->
			<div class="col position-relative">
				<div class="card shadow-none border-0 mb-0 rounded-0">
				<div class="card-body pb-0">
					<FullCalendar
					:options="calendarOptions"
					/>
				</div>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
import '@fullcalendar/core/vdom'
import FullCalendar from '@fullcalendar/vue'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import listPlugin from '@fullcalendar/list'
import interactionPlugin from '@fullcalendar/interaction'

import Datepicker from 'vuejs-datepicker'
import { en, ko } from "vuejs-datepicker/src/locale"

export default {
  components: {
    FullCalendar, Datepicker,
  },
  data() {
    return {
		colorLabelPp: false,
		
		selectDate: new Date(),
      	langKo: ko,

		// 이벤트추가및 수정 관련
		showDate: new Date(),
		id: 0,
		title: '',
		startDate: '',
		eventTime:50,
		time: '',
		labelLocal: { value: 1, color: '#cae4f7'},
		activePromptAddEvent: false,
		activePromptEditEvent: false,
		//----
		radios1: 'video',
		radios2: 80000,
		checkBox1: false,
		select1: { label: 1, value: 1},
		options1: [
			{ label: 1, value: 1},
			{ label: 2, value: 2}
		],
		select2: { label: "개인상담, 50분", value: 1},
		options2: [
			{ label: "개인상담, 50분", value: 1},
			{ label: "개인상담, 100분", value: 2}
		],
		select3: { label: "계좌이체", value: 1},
		options3: [
			{ label: "계좌이체", value: 1},
			{ label: "현장결제", value: 2}
		],
		select4: { label: "참석", value: 1},
		options4: [
			{ label: "참석", value: 1},
			{ label: "노쇼", value: 2},
			{ label: "취소", value: 3}
		],
		select5: { label: "미결제", value: 1},
		options5: [
			{ label: "미결제", value: 1},
			{ label: "결제완료", value: 2}
		],
		price: 0,
		name: "",
		memo: "",
		
		timePickerPp: false,
		timeData: [
          {
            list: ['오전','오후'],
            textAlign: 'center',
            className: 'scrollpicker-row-group'
          },
          {
            list: ['01', '02', '03', '04', '05', '06', '07', '08', '09', '10', '11', '12'],
            textAlign: 'center',
            className: 'scrollpicker-row-group'
          },
          {
            list: ['00','05', '10', '15', '20', '25', '30', '35', '40', '45', '50', '55'],
            textAlign: 'center',
            className: 'scrollpicker-row-group'
          }
        ],

		// 이벤트추가및 수정 관련 end

		event_list: [
				{ 	
					id: '123',
					title: '홍길동',
					start: '2021-07-08 12:00:00',
					end: '2021-07-08 16:00:00',
					color: '#cbc8ec',
					classNames: 'video'
					// textColor: '#fff' //글자색
				}
			],


		// 캘린더옵션

		calendarOptions: {
			plugins: [ 
				dayGridPlugin, 
				timeGridPlugin, 
				interactionPlugin,
				listPlugin
			],
			initialView: 'timeGridWeek',
			events: [
					{ 	
						id: '123',
						title: '홍길동',
						start: '2021-07-08 12:00:00',
						end: '2021-07-08 16:00:00',
						color: '#cbc8ec',
						classNames: 'video' // 화상상담일경우
					},
					{ 	
						id: '123',
						title: '홍길동',
						start: '2021-07-08 17:00:00',
						end: '2021-07-08 18:00:00',
						color: '#e0defb',
						classNames: 'video'
					},
					{ 	
						id: '123',
						title: '홍길동',
						start: '2021-07-08 19:00:00',
						end: '2021-07-08 20:00:00',
						color: '#cae4f7',
						classNames: 'call' //전화상담일경우
					},
					{ 	
						id: '123',
						title: '홍길동',
						start: '2021-07-08 21:00:00',
						end: '2021-07-08 22:00:00',
						color: '#e0defb',
						classNames: 'video'
					},
					{ 	
						id: '123',
						title: '홍길동',
						start: '2021-07-08 23:00:00',
						end: '2021-07-08 24:00:00',
						color: '#e0defb',
						classNames: 'video'
					},
			],
			eventTextColor: '#646186',
			headerToolbar: {
				left: 'prev,next today',
				center: 'title',
				// right: 'dayGridMonth,timeGridWeek,timeGridDay,list',
				right: 'dayGridMonth,timeGridWeek,timeGridDay',
			},
			buttonText:{
				today:    '오늘',
				month:    '월',
				week:     '주',
				day:      '일',
				// list:     '목록',
			},
		    editable: true,
			eventResizableFromStart: true,
			dragScroll: false,
			selectable: false,
			selectMirror: false,
			dayMaxEvents: 4, //4
			weekends: true,
			// select: this.handleDateSelect,
			dateClick: this.handleDateClick,
			// eventClick: this.handleEventClick,
			eventClick: this.handleEventClick,
			// eventsSet: this.handleEvents,
			// eventRender: this.eventRender,
			timeZone: 'local',
			locale: "ko",
			nowIndicator: true,

			// 사용된 캘린더 플러그인 docs
			// https://fullcalendar.io/docs

			/* you can update a remote database when these fire:
			eventAdd:
			eventChange:
			eventRemove:
			*/
		},
    }
  },
  mounted(){ 
  },
  watch:{
	activePromptAddEvent(){
		this.colorLabelPp = false;
	},
	activePromptEditEvent(){
		this.colorLabelPp = false;
	}
  },
  computed:{
    calendarLabels() {
        return [
			{value : 1, color: '#cae4f7' },
			{ value: 2, color: '#cbc8ec'},
			{ value: 3, color: '#e0defb'},
			{ value: 4, color: '#ecdaf3'},
		]
    },
  },
  methods: {
    handleDateClick: function(arg) {
        this.openAddNewEvent(arg.date);
        this.activePromptAddEvent = true;
    },
    openAddNewEvent(date) {
        this.clearFields();
        this.startDate = date;
        this.endDate = date;
    },
	handleEventClick: function() {
		this.openEditEvent(this.event_list[0]);
	    this.activePromptEditEvent = true;
	},
    openEditEvent(e) {
      this.id = e.id
      this.title = e.title
      this.startDate = e.startDate
      this.color = e.label
    },

    addEvent() {
        const obj = { title: this.title, startDate: this.startDate, color: this.labelLocal.color }
        this.$store.dispatch('calendar/addEvent', obj);
		alert("예약이 추가되었습니다.");
    },
    editEvent() {
      const obj = { title: this.title, startDate: this.startDate }
      this.$store.dispatch('calendar/editEvent', obj)
	  alert("예약이 수정되었습니다.");
    },

    clearFields() {
        this.title = this.endDate = this.url = "";
        this.id= 0;
        this.labelLocal = "none";
    },
    removeEvent() {
      this.$store.dispatch('calendar/removeEvent', this.id)
    },
    // eventDragged(event, date) {
    //   this.$store.dispatch('calendar/eventDragged', {event: event, date: date})
    // },
	// confirmTime(){
    //     const ciList = this.$refs.smoothPicker.getCurrentIndexList();
		
	// 	this.time = 
	// 	this.timeData[0].list[ciList[0]]+" "+
	// 	this.timeData[1].list[ciList[1]]+":"+
	// 	this.timeData[2].list[ciList[2]];
		
	// 	this.timePickerPp=false;
	// },
	clickLabel(label){
		this.labelLocal = label;
		this.colorLabelPp = false;
	}
  }
}
</script>
<style scoped>
	@import "../assets/css/calendar.css";
	
	.align-items-center{
		align-items: center;
	}
</style>
<style lang="scss">
	.small_select{
		width: 70px !important;
	}

	.v-select .vs__dropdown-menu{
		min-width: 0 !important;
	}
	.v-select .vs__dropdown-option{
		padding: 3px 10px;
	}
	.left_bottom_menu .vs-component.vs-button.vs-button-danger.vs-button-border{
		border: none !important;
	}

	.border-left-none .vs-con-input i{
		border-left: none !important;
	}


	@media screen  and (max-width: 767px){
		.fc-view-harness .fc-view {
			overflow-y: scroll;
		}
		.mb_time_picker_pp{
			width: 100%;
			height: 100vh;
			position: fixed;
			bottom: 0%;
			left: 50%;
			transform: translateX(-50%);
			z-index: 99999;
